import React, { useState, useEffect } from 'react'
import { Row, Col, Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { TextField } from '@material-ui/core';
import mKategoriProduk from '../../../../Controler/c_KategoriProduk'
import validate from 'validate.js'
import Notifikasi from '../../../../helpers/Notifikasi'
import { isArray } from "util";
import IntlMessages from "../../../../helpers/IntlMessages"

const ModalAdmin = (props) => {
    const clientID = props.clientID
    const token = props.token
    const detail = props.detailKategoriProduk || {}
    const klinik = props.klinik
    console.log(props, 'props')
    const [form, setForm] = useState({
        kodeKategoriProduk: '',
        kategoriProduk: '',
    })
    useEffect(() => {
        if (detail.kodeKategoriProduk) {
            setForm({
                kodeKategoriProduk: detail.kodeKategoriProduk,
                kategoriProduk: detail.kategoriProduk,
            })
        }
        return () => {

        }
    }, [detail])
    const handleClose = () => {
        props.setOpen(false)
    }
    const handleChange = (ev) => {
        var value = ev.target.value;
        const key = ev.target.id || ev.target.name;
        var newForm = { ...form }
        newForm[key] = value;
        setForm(newForm);
    }
    const validateForm = () => {
        const { kodeKategoriProduk, kategoriProduk } = form;
        const promForm = new Promise((resolve, reject) => {
            const constraint = {
                kategoriProduk: {
                    length: {
                        minimum: 3,
                        message: "kategori produk minimal terdiri dari 3 huruf"
                    },
                }
            }
            const validator = validate({ kodeKategoriProduk, kategoriProduk }, constraint, { fullMessages: false });
            if (validator === undefined) {
                resolve();
            } else {
                reject(validator);
            }
        })
        return Promise.all([promForm]);
    }
    const handleSimpan = () => {
        validateForm().then(() => {
            mKategoriProduk.simpanKategoriProduk(clientID, token, klinik.kodeklinik, form.kodeKategoriProduk, form.kategoriProduk).then(resp => {
                if (resp) {
                    Notifikasi("success", "Berhasil disimpan", "")
                    props.setIsReload(true)
                    handleClose()
                } else {
                    Notifikasi("error", "Server tidak dapat menjalankan perintah Anda saat ini. Silakan coba sesaat lagi", "Gagal Simpan")
                }
            }).catch(err => {
                if (isArray(err) && err.length > 0) {
                    Notifikasi("error", err[0].message, "Gagal Simpan")
                } else if (typeof err) {
                    Notifikasi("error", err.message, "Gagal Simpan")
                } else {
                    console.log(err);
                }
            })
        }).catch(err => {
            const keys = Object.keys(err);
            for (const key of keys) {
                Notifikasi("warning", err[key])
            }
        })
    }
    return (
        <Modal
            isOpen={props.open}
            toggle={handleClose}
            wrapClassName="modal-right"
        >
            <ModalHeader toggle={handleClose}>
                {detail.kodeKategoriProduk ? ("Update") : ("Add")} <IntlMessages id="menu.kategori" />
                    </ModalHeader>
            <ModalBody>
                <Row>
                    <Col xs={12} md={12} className="marginFrom">
                        <TextField
                            id="kategoriProduk"
                            name="kategoriProduk"
                            label="Kategori Produk"
                            fullWidth
                            value={form.kategoriProduk}
                            onChange={handleChange}
                            variant="outlined"
                        />
                    </Col>
                </Row>
            </ModalBody>
            <ModalFooter>
                <Button color="secondary" onClick={handleClose}>Cancel</Button>
                <Button color="primary" onClick={handleSimpan}>Save <IntlMessages id="menu.kategori" /></Button>{" "}
            </ModalFooter>
        </Modal>
    )
}

export default ModalAdmin;