import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import KategoriProduk from './KategoriProduk'


const Gogo = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/index`} to={`${match.url}/index`} />
      <Route
        path={`${match.url}`}
        render={props => <KategoriProduk {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);
export default Gogo;
