import GQL from '../helpers/GQL'


const User = {
	dataUser: (clientID, token, username, jenisUser) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    detailUser(username:"${username}",jenisUser:"${jenisUser}"){
                        superadmin
                        nama
                        noHp
                        jenisUser
                  }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.detailUser) return (resolve(resp.data.detailUser));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	selectKlinikPasien: (clientID, token, kodeklinik, namaklinik, username) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				updateLogin(kodeklinik: "${kodeklinik}", namaklinik: "${namaklinik}")
				simpanUser(input:{username:"${username}"}, kodeklinik:"${kodeklinik}", namaklinik: "${namaklinik}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.updateLogin && resp.data.simpanUser) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	selectKlinikAdmin: (clientID, token, kodeklinik, namaklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				updateLogin(kodeklinik: "${kodeklinik}", namaklinik: "${namaklinik}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.updateLogin) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	hapusUser: (clientID, token, username) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				hapusUser(username: "${username}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.hapusUser) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	registerUser: (username, nama, alamat, password, noHp) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				registerUser(input:{
					username:"${username}", 
					password:"${password}", 
					nama:"${nama}", 
					noHp:"${noHp}", 
					alamat:"${alamat}"})
			  }
            `
			GQL(query).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.registerUser) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	aktivasiUser: (username, salt) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				aktivasiUser(username:"${username}", salt: "${salt}")
			  }
            `
			GQL(query).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.aktivasiUser) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	forgetPassword: (username) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				reqResetPassword(username:"${username}")
			  }
            `
			GQL(query).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.reqResetPassword) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	resetPassword: (username, salt, password) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				resetNewPassword(username:"${username}", salt: "${salt}", password:"${password}")
			  }
            `
			GQL(query).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.resetNewPassword) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	loginUser: (username, password) => {
		return new Promise((resolve, reject) => {
			const query = `
			query {
				loginUser(username:"${username}",password:"${password}"){
					clientID
					token
					superadmin
					jenisUser
					klinik{
						kodeklinik
						namaklinik
					}
				}
			  }
            `
			GQL(query).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.loginUser) return (resolve(resp.data.loginUser));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	detailUser: (clientID, token, username, jenisUser) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    detailUser(username:"${username}",jenisUser:"${jenisUser}"){
                        username
                        nama
						noHp
						alamat
                  }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.detailUser) return (resolve(resp.data.detailUser));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
	updateProfil: (clientID, token, username,  nama, noHp, alamat, jenisUser,  kodeklinik, namaklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
				simpanUser(input:{username:"${username}", nama:"${nama}", noHp:"${noHp}", alamat:"${alamat}", jenisUser:"${jenisUser}"},kodeklinik: "${kodeklinik}", namaklinik: "${namaklinik}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.simpanUser) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
};

export default User;
