import GQL from '../helpers/GQL'


const KategoriProduk = {
	listKategoriProduk: (clientID, token, kodeklinik) => {
		return new Promise((resolve, reject) => {
			const query = `
                query {
                    listKategoriProduk(kodeklinik:"${kodeklinik}"){
                        kodeKategoriProduk
                        kategoriProduk
                      }
                }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));;
				if (resp.data && resp.data.listKategoriProduk) return (resolve(resp.data.listKategoriProduk));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
    },
    simpanKategoriProduk: (clientID, token,  kodeklinik, kodeKategoriProduk, KategoriProduk) => {
        return new Promise((resolve, reject) => {
            const query =`
            mutation {
                simpanKategoriProduk(input:{
                    kodeKategoriProduk:"${kodeKategoriProduk}", 
                    kategoriProduk:"${KategoriProduk}",
                    kodeklinik: "${kodeklinik}"})
            }
        `
            GQL(query, clientID, token).then(resp => {
                if (resp.errors) return (reject(resp.errors));;
                if (resp.data && resp.data.simpanKlinik) return (resolve(resp.data.simpanKlinik));
                return (resolve([]))
            }).catch(err => {
                return (reject(err));
            })
        })
    },
	hapusKategoriProduk: (clientID, token, kodeklinik, kodeKategoriProduk) => {
		return new Promise((resolve, reject) => {
			const query = `
			mutation {
                hapusKategoriProduk(kodeKategoriProduk:"${kodeKategoriProduk}", kodeklinik:"${kodeklinik}")
			  }
            `
			GQL(query, clientID, token).then(resp => {
				if (resp.errors) return (reject(resp.errors));
				if (resp.data && resp.data.hapusKategoriProduk ) return (resolve(true));
				return (resolve([]))
			}).catch(err => {
				return (reject(err));
			})
		})
	},
};

export default KategoriProduk;
